
export const doLogin = async (table, database, handleVideoUpdate) => {
  // await database.collection('video').doc(table).remove()
  database.collection('video').doc(table).onSnapshot(snapshot => {
    handleVideoUpdate(snapshot.data(), table)
  });
}

export const doOffer = async (table, offer, database, username) => {
  await database.ref('/video/' + table).set({
    type: 'offer',
    from: username,
    offer: JSON.stringify(offer)
  })
}

export const doAnswer = async (table, answer, database, username) => {
  await database.ref('/video/' + table).update({
    type: 'answer',
    from: username,
    answer: JSON.stringify(answer)
  })
}

export const doLeaveNotif = async (table, database, username) => {
  await database.ref('/video/' + table).update({
    type: 'leave',
    from: username
  })
}

export const doCandidate = async (table, candidate, database, username) => {
  // send the new candiate to the peer
  await database.ref('/video/' + table).update({
    type: 'candidate',
    from: username,
    candidate: JSON.stringify(candidate)
  })
}
